import {setToken} from "../../plugins/auth";
import {initWebSocket} from "@/plugins/initWebsocket";

export default {
  name: "Login",
  layout: 'single-container',
  data() {
    return {
      showPsw: false,
      oLogin: {}
    }
  },
  computed: {
    btnDisable() {
      var reg = new RegExp("^[a-zA-Z0-9]+([._\\-]*[A-Za-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$"); //正则表达式
      if (!this.oLogin.sEmail || !reg.test(this.oLogin.sEmail))
        return true;
      // return !this.oLogin.sPassword || this.oLogin.sPassword.length < 16;
      return !this.oLogin.sPassword

    }
  },
  methods: {
    async handleLogin() {
      let data = {
        ...this.oLogin,
        iSource: 1,       //来源：1-web 2-app
        iLoginType: 2,    //登录类型：1-手机号 2-邮箱
      }
      let res = await this.$axios.post('/v1/auth/UserLogin', data)
      console.log(res);
      localStorage.setItem('Email', this.oLogin.sEmail)
      setToken(res.sSessionKey)
      initWebSocket(res.iUserId);
      localStorage.setItem('sInviteCode', res.sInviteCode)
      localStorage.setItem('sEmail', res.sEmail)
      await this.$axios.post('/v1/udc/GetUserExten', data)
      this.$store.commit('SET_ISLOGIN', true)
      this.$toast(this.$t('Account19'))
      this.$router.replace('/')
    }
  },
}
